var Cleave = require('cleave.js')
require('../../node_modules/cleave.js/dist/addons/cleave-phone.ru')

if(document.getElementById('modal-form') != null) {
  var modalFormCont = $('#modal-form-cont');
  var modalForm = $('#modal-form');
  var openForm = $('.open-form');
  var mainWrapper = $('#main-wrapper');

  var cleave = new Cleave('.phone-input', {
    phone: true,
    phoneRegionCode: 'ru'
  });

  

  openForm.click(function() {
    modalFormCont.fadeIn(300);
    mainWrapper.css('filter', 'blur(3px)');
    $('body').css('overflow', 'hidden');
  })

  modalFormCont.click(function(e) {
    if(e.target.classList.contains('close-form')) {
      modalFormCont.fadeOut(300);
      mainWrapper.css('filter', 'blur(0px)');
      $('body').css('overflow', 'visible');
    }
  })
}